import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViewSubTypeOfHousingComponent } from './view-sub-type-of-housing.component';

const routes: Routes = [
  {
    path: '',
    component: ViewSubTypeOfHousingComponent,
    data: {
      title: 'View Sub Type of Housing'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ViewSubTypeOfHousingRoutingModule {}



